/* Header.css */
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 30px;
    background-color: #f5a4cb;
    box-shadow: 0 2px 10px rgba(244, 114, 182, 0.15);
    position: sticky;
    top: 0;
    z-index: 100;
  }
  
  .logo h1 {
    color: #e83e8c;
    font-size: 24px;
    letter-spacing: 0.5px;
    margin: 0;
  }
  
  .logo span {
    color: #e83e8c;
    font-weight: bold;
  }
  
  .logo a {
    text-decoration: none;
  }
  
  .nav-links {
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  .nav-links li {
    position: relative;
    margin: 0 15px;
  }
  
  .nav-links li a {
    color: #444;
    text-decoration: none;
    font-size: 16px;
    font-weight: 500;
    transition: color 0.3s ease;
    padding: 10px 0;
    display: flex;
    align-items: center;
  }
  
  .nav-links li a:hover {
    color: #e83e8c;
  }
  
  .nav-links li a svg {
    margin-left: 5px;
  }
  
  /* Dropdown styles */
  .dropdown {
    position: relative;
  }
  
  .dropdown-content {
    position: absolute;
    background-color: white;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
    width: 220px;
    border-radius: 8px;
    opacity: 0;
    visibility: hidden;
    transform: translateY(10px);
    transition: all 0.3s ease;
    z-index: 1000;
  }
  
  .dropdown:hover .dropdown-content {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
  }
  
  .dropdown-content a {
    color: #444;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    border-bottom: 1px solid #f1f1f1;
    transition: background-color 0.3s ease;
  }
  
  .dropdown-content a:last-child {
    border-bottom: none;
  }
  
  .dropdown-content a:hover {
    background-color: #fceff5;
    color: #e83e8c;
  }
  
  /* User account dropdown */
  .user-account {
    position: relative;
  }
  
  .user-btn {
    display: flex;
    align-items: center;
    background: none;
    border: none;
    cursor: pointer;
    color: #444;
    font-size: 16px;
    font-weight: 500;
    padding: 8px 15px;
    border-radius: 30px;
    transition: background-color 0.3s ease;
  }
  
  .user-btn:hover {
    background-color: #fceff5;
    color: #e83e8c;
  }
  
  .user-btn svg:first-child {
    margin-right: 8px;
    color: #e83e8c;
  }
  
  .user-btn svg:last-child {
    margin-left: 8px;
  }
  
  .user-dropdown {
    position: relative;
  }
  
  .user-dropdown-content {
    position: absolute;
    right: 0;
    margin-top: 5px;
    background-color: white;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
    min-width: 160px;
    border-radius: 8px;
    z-index: 1000;
  }
  
  .user-dropdown-content a {
    color: #444;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    border-bottom: 1px solid #f1f1f1;
    transition: background-color 0.3s ease;
  }
  
  .user-dropdown-content a:hover {
    background-color: #fceff5;
    color: #e83e8c;
  }
  
  .logout-btn {
    width: 100%;
    text-align: left;
    color: #e83e8c;
    padding: 12px 16px;
    background: none;
    border: none;
    cursor: pointer;
    font-size: 16px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .logout-btn:hover {
    background-color: #fceff5;
  }
  
  /* Media queries for responsiveness */
  @media screen and (max-width: 768px) {
    .header {
      flex-direction: column;
      padding: 15px;
    }
    
    .logo {
      margin-bottom: 15px;
    }
    
    .nav-links {
      width: 100%;
      flex-direction: column;
      text-align: center;
    }
    
    .nav-links li {
      margin: 10px 0;
    }
    
    .dropdown-content {
      position: static;
      opacity: 1;
      visibility: visible;
      transform: none;
      width: 100%;
      box-shadow: none;
      display: none;
    }
    
    .dropdown:hover .dropdown-content {
      display: block;
    }
    
    .user-account {
      margin-top: 15px;
    }
    
    .user-dropdown-content {
      right: auto;
      left: 50%;
      transform: translateX(-50%);
    }
  }