/* Footer.css */
.footer {
    background-color: #fdf2f8;
    color: #444;
    padding: 50px 0 20px;
    border-top: 5px solid #e83e8c;
    margin-top: 0;
  }
  
  .footer-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  .footer-section {
    flex: 1;
    padding: 0 20px;
    min-width: 250px;
    margin-bottom: 30px;
  }
  
  .footer-section h2 {
    color: #e83e8c;
    font-size: 20px;
    margin-bottom: 20px;
    position: relative;
    padding-bottom: 10px;
  }
  
  .footer-section h2:after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 50px;
    height: 2px;
    background-color: #e83e8c;
  }
  
  .logo-text h2 {
    color: #e83e8c;
    font-size: 24px;
    margin-bottom: 15px;
  }
  
  .logo-text span {
    color: #7c3aed;
    font-weight: 300;
  }
  
  .about p {
    margin-bottom: 20px;
    line-height: 1.6;
  }
  
  .contact p {
    margin-bottom: 10px;
    display: flex;
    align-items: center;
  }
  
  .socials {
    margin-top: 20px;
    display: flex;
  }
  
  .socials a {
    width: 36px;
    height: 36px;
    background-color: white;
    border-radius: 50%;
    margin-right: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #e83e8c;
    text-decoration: none;
    transition: all 0.3s ease;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .socials a:hover {
    background-color: #e83e8c;
    color: white;
    transform: translateY(-3px);
  }
  
  .links ul, .resources ul {
    list-style: none;
    padding: 0;
  }
  
  .links ul li, .resources ul li {
    margin-bottom: 10px;
  }
  
  .links ul li a, .resources ul li a {
    color: #444;
    text-decoration: none;
    transition: color 0.3s ease;
    display: inline-block;
    position: relative;
  }
  
  .links ul li a:hover, .resources ul li a:hover {
    color: #e83e8c;
    padding-left: 5px;
  }
  
  .links ul li a:before, .resources ul li a:before {
    content: 'â€º';
    position: absolute;
    left: -15px;
    opacity: 0;
    transition: all 0.3s ease;
    color: #e83e8c;
  }
  
  .links ul li a:hover:before, .resources ul li a:hover:before {
    opacity: 1;
    left: -10px;
  }
  
  .footer-bottom {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    text-align: center;
    border-top: 1px solid #f0d4e3;
    margin-top: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
  }
  
  .footer-bottom p {
    margin: 0;
  }
  
  .footer-links {
    display: flex;
    gap: 20px;
    flex-wrap: wrap;
  }
  
  .footer-links a {
    color: #444;
    text-decoration: none;
    transition: color 0.3s ease;
  }
  
  .footer-links a:hover {
    color: #e83e8c;
  }
  
  @media screen and (max-width: 768px) {
    .footer-content {
      flex-direction: column;
    }
    
    .footer-section {
      padding: 0;
      margin-bottom: 40px;
    }
    
    .footer-bottom {
      flex-direction: column;
      gap: 15px;
    }
    
    .footer-links {
      justify-content: center;
    }
  }