/* LandingPage.css */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
  line-height: 1.6;
  color: #eb45bc;
}

.landing-container {
  height: 100vh;
  width: 100%;
  background-image: url('../Assets/bcs-landing.jpg');
  background-size: cover;
  background-position: center;
  position: relative;
}

.overlay {
  background: rgba(255, 255, 255, 0.493);
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.landing-content {
  max-width: 900px;
  padding: 0 20px;
}

.landing-content h1 {
  font-size: 3rem;
  color: #ef2296;
  margin-bottom: 1rem;
}

.landing-content p {
  font-size: 1.2rem;
  color: #34495e;
  margin-bottom: 2rem;
}

.buttons-container {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 3rem;
}

.btn {
  padding: 12px 30px;
  font-size: 1rem;
  border-radius: 30px;
  cursor: pointer;
  font-weight: 600;
  text-decoration: none;
  transition: all 0.3s ease;
}

.btn-primary {
  background-color: #db34b7;
  color: white;
  border: none;
}

.btn-primary:hover {
  background-color: #b929a6;
  transform: translateY(-3px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.btn-secondary {
  background-color: transparent;
  color: #db34cd;
  border: 2px solid #db34b1;
}

.btn-secondary:hover {
  background-color: #db34bc;
  color: white;
  transform: translateY(-3px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.features {
    display: flex;
    justify-content: space-between;
    gap: 30px;
    margin-top: 3rem;
  }
  
  .feature-item {
    flex: 1;
    padding: 30px 20px;
    background-color: rgba(255, 255, 255, 0.705);
    border-radius: 10px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease;
  }
  
  .feature-item:hover {
    transform: translateY(-10px);
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
  }
  
  .feature-icon {
    width: 80px;
    height: 80px;
    background-color: rgba(52, 152, 219, 0.1);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto 20px;
    overflow: hidden;
  }
  
  .feature-icon img {
    width: 60%;
    height: 60%;
    object-fit: contain;
  }
  
  .feature-item h3 {
    font-size: 1.3rem;
    margin-bottom: 10px;
    color: #2c3e50;
  }
  
  .feature-item p {
    font-size: 0.9rem;
    color: #7f8c8d;
  }
  
/* Mobile Responsive */
@media (max-width: 768px) {
  .landing-content h1 {
    font-size: 2.2rem;
  }
  
  .landing-content p {
    font-size: 1rem;
  }
  
  .features {
    flex-direction: column;
  }
  
  .buttons-container {
    flex-direction: column;
    gap: 15px;
  }
  
  .btn {
    width: 80%;
    margin: 0 auto;
  }
}